import React, { useContext, useMemo } from 'react';
import { SeccionContext, SeccionType } from '../Context/Seccion'
export default function Footer() {
    const { data: { online } } = useContext(SeccionContext) as SeccionType;
    return <footer className={"main-footer" + (online ? '' : ' bg-danger')} style={{ position: 'fixed', bottom: 0, left: 0, right: 0 }}>
        {online ?<>
            <div className="float-right hidden-xs"><b>Version</b> 2.2.5</div>
            <strong> <a href="http://ailanthus-sistems.com/"><img src="/logos/logo-text.svg" width="150px" alt='logo' /></a> Copyright ¬© 2021-2025.</strong>
        </>:<>
            <strong> 
                SE ENCUENTRA ACTULAMENTE SIN CONECCION A INTERNET, PODRA NAVEGAR Y MONTAR LAS OPERACIONES PERO NO PODRA PROCESARLAS TOMAR PREVISIONES
            </strong>
        </>}
    </footer>
}